import api from './api';
import axios from 'axios';

// OSSÊìç‰ΩúÁõ∏ÂÖ≥API
const ossApi = {
  /**
   * Ëé∑ÂèñOSSÈÖçÁΩÆÔºà‰∏¥Êó∂Âá≠ËØÅÔºâ
   * @returns {Promise<Object>} OSSÈÖçÁΩÆ‰ø°ÊÅØ
   */
  getOSSConfig() {
    return api.get('/oss/config');
  },

  /**
   * Ëé∑ÂèñOSS‰∏ä‰º†Á≠ñÁï•ÔºàPolicyÔºâ
   * @param {string} dir ‰∏ä‰º†ÁõÆÂΩï
   * @returns {Promise<Object>} ‰∏ä‰º†Á≠ñÁï•
   */
  getOSSPolicy(dir = 'posts/') {
    return api.get('/oss/policy', { params: { dir } });
  },

  /**
   * Áõ¥Êé•‰∏ä‰º†Êñá‰ª∂Âà∞ÈòøÈáå‰∫ëOSS
   * @param {File} file Ë¶Å‰∏ä‰º†ÁöÑÊñá‰ª∂ÂØπË±°
   * @param {string} dir ‰∏ä‰º†ÁõÆÂΩï
   * @returns {Promise<string>} Êñá‰ª∂ÁöÑURL
   */
  async uploadFile(file, dir = 'posts/') {
    try {
      // 1. Ëé∑Âèñ‰∏ä‰º†Á≠ñÁï•
      const policyRes = await this.getOSSPolicy(dir);
      const policy = policyRes.policy;
      
      if (!policy) {
        throw new Error('Ëé∑Âèñ‰∏ä‰º†Á≠ñÁï•Â§±Ë¥•');
      }
      
      // Ê£ÄÊü•Âπ∂Â§ÑÁêÜhostÔºåÁ°Æ‰øùÊòØ‰∏Ä‰∏™ÊúâÊïàÁöÑURLÂ≠óÁ¨¶‰∏≤
      let host = policy.host;
      if (typeof host === 'object') {
        console.error('Host ÊòØ‰∏Ä‰∏™ÂØπË±°ËÄå‰∏çÊòØÂ≠óÁ¨¶‰∏≤:', host);
        // Â∞ùËØï‰ªéÂØπË±°‰∏≠Ëé∑ÂèñURLÔºåÊàñËÄÖÊûÑÂª∫‰∏Ä‰∏™ÈªòËÆ§URL
        host = `https://${policy.bucket}.${policy.region}.aliyuncs.com`;
      }
      
      // Á°Æ‰øùhostÊòØ‰∏Ä‰∏™ÊúâÊïàÁöÑURLÂ≠óÁ¨¶‰∏≤
      if (!host.startsWith('http')) {
        host = `https://${host}`;
      }
      
      // 2. ÂáÜÂ§áË°®ÂçïÊï∞ÊçÆ
      const formData = new FormData();
      const key = `${policy.key}${file.name.substring(file.name.lastIndexOf('.'))}`;
      
      formData.append('key', key);
      formData.append('OSSAccessKeyId', policy.accessKeyId);
      formData.append('policy', policy.policyBase64);
      formData.append('signature', policy.signature);
      formData.append('success_action_status', '200');
      formData.append('file', file);
      
      console.log('Ê≠£Âú®‰∏ä‰º†Êñá‰ª∂Âà∞:', host);
      
      // 3. ‰∏ä‰º†Êñá‰ª∂Âà∞OSS
      await axios.post(host, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      
      // 4. ËøîÂõûÊñá‰ª∂URL
      let cdnHost = policy.cdnHost || host;
      if (typeof cdnHost === 'object') {
        cdnHost = host; // Â¶ÇÊûúcdnHost‰πüÊòØÂØπË±°ÔºåÂàô‰ΩøÁî®host
      }
      
      // Á°Æ‰øùURLÊ†ºÂºèÊ≠£Á°Æ
      const fileUrl = `${cdnHost}/${key}`;
      console.log('Êñá‰ª∂‰∏ä‰º†ÊàêÂäüÔºåURL:', fileUrl);
      
      return fileUrl;
    } catch (error) {
      console.error('‰∏ä‰º†Êñá‰ª∂Âà∞OSSÂ§±Ë¥•:', error);
      throw error;
    }
  },
  
  /**
   * ÊâπÈáè‰∏ä‰º†Êñá‰ª∂Âà∞ÈòøÈáå‰∫ëOSS
   * @param {Array<File>} files Ë¶Å‰∏ä‰º†ÁöÑÊñá‰ª∂ÂØπË±°Êï∞ÁªÑ
   * @param {string} dir ‰∏ä‰º†ÁõÆÂΩï
   * @returns {Promise<Array<string>>} Êñá‰ª∂URLÊï∞ÁªÑ
   */
  async uploadMultipleFiles(files, dir = 'posts/') {
    if (!files || files.length === 0) {
      return [];
    }
    
    try {
      // ÂêåÊó∂Ëé∑Âèñ‰∏ÄÊ¨°‰∏ä‰º†Á≠ñÁï•
      const policyRes = await this.getOSSPolicy(dir);
      const policy = policyRes.policy;
      
      if (!policy) {
        throw new Error('Ëé∑Âèñ‰∏ä‰º†Á≠ñÁï•Â§±Ë¥•');
      }
      
      // Ê£ÄÊü•Âπ∂Â§ÑÁêÜhostÔºåÁ°Æ‰øùÊòØ‰∏Ä‰∏™ÊúâÊïàÁöÑURLÂ≠óÁ¨¶‰∏≤
      let host = policy.host;
      if (typeof host === 'object') {
        console.error('Host ÊòØ‰∏Ä‰∏™ÂØπË±°ËÄå‰∏çÊòØÂ≠óÁ¨¶‰∏≤:', host);
        // Â∞ùËØï‰ªéÂØπË±°‰∏≠Ëé∑ÂèñURLÔºåÊàñËÄÖÊûÑÂª∫‰∏Ä‰∏™ÈªòËÆ§URL
        host = `https://${policy.bucket}.${policy.region}.aliyuncs.com`;
      }
      
      // Á°Æ‰øùhostÊòØ‰∏Ä‰∏™ÊúâÊïàÁöÑURLÂ≠óÁ¨¶‰∏≤
      if (!host.startsWith('http')) {
        host = `https://${host}`;
      }
      
      console.log('ÂáÜÂ§áÊâπÈáè‰∏ä‰º†Êñá‰ª∂Âà∞:', host);
      
      // Âπ∂Ë°å‰∏ä‰º†ÊâÄÊúâÊñá‰ª∂
      const uploadPromises = files.map(async (file, index) => {
        try {
          const formData = new FormData();
          const key = `${policy.key}-${index}${file.name.substring(file.name.lastIndexOf('.'))}`;
          
          formData.append('key', key);
          formData.append('OSSAccessKeyId', policy.accessKeyId);
          formData.append('policy', policy.policyBase64);
          formData.append('signature', policy.signature);
          formData.append('success_action_status', '200');
          formData.append('file', file);
          
          await axios.post(host, formData, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          });
          
          // Â§ÑÁêÜËøîÂõûURL
          let cdnHost = policy.cdnHost || host;
          if (typeof cdnHost === 'object') {
            cdnHost = host; // Â¶ÇÊûúcdnHost‰πüÊòØÂØπË±°ÔºåÂàô‰ΩøÁî®host
          }
          
          const fileUrl = `${cdnHost}/${key}`;
          console.log(`Êñá‰ª∂ ${index+1}/${files.length} ‰∏ä‰º†ÊàêÂäü:`, fileUrl);
          return fileUrl;
        } catch (error) {
          console.error(`Êñá‰ª∂ ${index+1}/${files.length} ‰∏ä‰º†Â§±Ë¥•:`, error);
          throw error;
        }
      });
      
      // Á≠âÂæÖÊâÄÊúâ‰∏ä‰º†ÂÆåÊàê
      const fileUrls = await Promise.all(uploadPromises);
      console.log('ÊâÄÊúâÊñá‰ª∂‰∏ä‰º†ÂÆåÊàêÔºåURLÂàóË°®:', fileUrls);
      return fileUrls;
    } catch (error) {
      console.error('ÊâπÈáè‰∏ä‰º†Êñá‰ª∂Âà∞OSSÂ§±Ë¥•:', error);
      throw error;
    }
  }
};

export default ossApi; 