    <!-- Êñ∞Â¢û/ÁºñËæëËØ¥ËØ¥/Êó•ËÆ∞ÂØπËØùÊ°Ü -->
    <el-dialog
      v-model="showPostDialog"
      :title="isEditing ? (postType === 'diary' ? 'ÁºñËæëÊó•ËÆ∞' : 'ÁºñËæëËØ¥ËØ¥') : (postType === 'diary' ? 'ÂÜôÊó•ËÆ∞' : 'ÂèëËØ¥ËØ¥')"
      width="65%"
