          </div>
        </el-form-item>
      </el-form>
      
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="showPostDialog = false">ÂèñÊ∂à</el-button>
          <el-button type="primary" @click="handleSubmit" :disabled="!isPostValid" :size="postType === 'diary' ? 'large' : 'default'">
            {{ isEditing ? '‰øùÂ≠ò‰øÆÊîπ' : (postType === 'diary' ? '‰øùÂ≠òÊó•ËÆ∞' : 'ÂèëÂ∏ÉËØ¥ËØ¥') }}
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, computed, watch, onMounted } from 'vue'
import { usePostStore } from '../stores/post'
import { format } from 'date-fns'
import { 
  Plus, 
  Location, 
  Edit, 
  Document,
  ChatDotRound, 
  Notebook,
  StarFilled,
  Sunny
} from '@element-plus/icons-vue'
import { ElMessage, ElLoading } from 'element-plus'
import TimeLinePage from '@/components/TimeLinePage.vue'
import ossApi from '@/services/ossApi' // ÂØºÂÖ•OSS APIÊúçÂä°

const postStore = usePostStore()

const showPostDialog = ref(false)
const locationVisible = ref(false)
const weatherVisible = ref(false)
const showMoodSelector = ref(false)
const postType = ref('thought')
const activeFilter = ref('all')
const isEditing = ref(false)
const editingPostId = ref(null)
const fileList = ref([]) // Êñá‰ª∂ÂàóË°®
const dialogImageUrl = ref('') // È¢ÑËßàÂõæÁâáURL
const dialogVisible = ref(false) // È¢ÑËßàÂØπËØùÊ°ÜÊòæÁ§∫Áä∂ÊÄÅ
const uploadLoading = ref(false) // ‰∏ä‰º†Âä†ËΩΩÁä∂ÊÄÅ

const newPost = ref({
  title: '',
  content: '',
  images: [],
  uploadFiles: [], // Ê∏ÖÁ©∫‰∏ä‰º†Êñá‰ª∂ÂàóË°®
  location: '',
  mood: '',
  weather: '',
  type: 'thought'
})

// ÂøÉÊÉÖÈÄâÈ°π
const moods = [
  { emoji: 'üòä', text: 'ÂºÄÂøÉ', value: 'happy' },
  { emoji: 'üò¢', text: '‰º§ÂøÉ', value: 'sad' },
  { emoji: 'üò°', text: 'ÁîüÊ∞î', value: 'angry' },
  { emoji: 'üò¥', text: 'Áñ≤ÊÉ´', value: 'tired' },
  { emoji: 'ü•∞', text: 'Áà±ÊÑè', value: 'love' },
  { emoji: 'ü§î', text: 'ÊÄùËÄÉ', value: 'thinking' }
]

// Ëé∑ÂèñÂøÉÊÉÖÊñáÊú¨
const getMoodText = (moodValue) => {
  const mood = moods.find(m => m.value === moodValue)
  return mood ? `${mood.emoji} ${mood.text}` : ''
}

// ÂàáÊç¢ÂøÉÊÉÖÈÄâÊã©Âô®
const toggleMood = () => {
  showMoodSelector.value = !showMoodSelector.value
}

// ÈÄâÊã©ÂøÉÊÉÖ
const selectMood = (moodValue) => {
  newPost.value.mood = moodValue
  showMoodSelector.value = false
}

// Ê£ÄÊü•ÂèëÂ∏ÉÂÜÖÂÆπÊòØÂê¶ÊúâÊïà
const isPostValid = computed(() => {
  if (postType.value === 'diary') {
    return newPost.value.title.trim() && newPost.value.content.trim()
  } else {
    return newPost.value.content.trim()
  }
})

// Ê†ºÂºèÂåñÊó•Êúü
const formatDate = (dateString) => {
  if (!dateString) return 'ÂàöÂàö'
  
  const date = new Date(dateString)
  if (isNaN(date.getTime())) {
    console.warn('Êó†ÊïàÁöÑÊó•ÊúüÊ†ºÂºè:', dateString)
    return 'ÂàöÂàö'
  }
  
  return date.toLocaleString('zh-CN', {
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit',
    hour12: false
  })
}

// Ê†ºÂºèÂåñÊó∂Èó¥
const formatPostTime = (createdAt) => {
  if (!createdAt) {
    console.warn('Áº∫Â∞ëÂàõÂª∫Êó∂Èó¥Ôºå‰ΩøÁî®ÂΩìÂâçÊó∂Èó¥');
    return format(new Date(), 'yyyy-MM-dd HH:mm');
  }
  
  try {
    const date = new Date(createdAt);
    if (!isNaN(date.getTime())) {
      return format(date, 'yyyy-MM-dd HH:mm');
    } else {
      console.warn('Êó†ÊïàÁöÑÊó•ÊúüÊ†ºÂºè:', createdAt);
    }
  } catch (error) {
    console.error('Êó•ÊúüÊ†ºÂºèÂåñÈîôËØØ:', error);
  }
  
  return format(new Date(), 'yyyy-MM-dd HH:mm');
}

// Ê∑ªÂä†Â∏ñÂ≠ê
const addPost = async () => {
  if (!isPostValid.value) return
  
  try {
    const postData = {
      ...newPost.value,
      createdAt: new Date().toISOString() // Á°Æ‰øùËÆæÁΩÆÂàõÂª∫Êó∂Èó¥
    }
    
    const success = await postStore.addCustomPost(postData)
    if (success) {
      resetForm()
      showPostDialog.value = false
      await loadPosts()
    }
  } catch (error) {
    console.error('ÂèëÂ∏ÉÂ§±Ë¥•:', error)
    ElMessage.error('ÂèëÂ∏ÉÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÂÜçËØï')
  }
}

// Ê†ºÂºèÂåñÂπ∂ËøáÊª§Â∏ñÂ≠êÊï∞ÊçÆ‰ª•ÈÄÇÂ∫îTimeLinePageÁªÑ‰ª∂
const filteredPosts = computed(() => {
  if (!Array.isArray(postStore.posts)) {
    console.warn('posts ‰∏çÊòØÊï∞ÁªÑ:', postStore.posts)
    return []
  }
  
  let result = [...postStore.posts]
  
  // Ë∞ÉËØïÔºöÊâìÂç∞ÂéüÂßãÊï∞ÊçÆ
  console.log('ÂéüÂßãÂ∏ñÂ≠êÊï∞ÊçÆ:', result)
  
  // ÊåâÁ±ªÂûãËøáÊª§
  if (activeFilter.value !== 'all') {
    result = result.filter(post => post.type === activeFilter.value)
  }
  
  // ÊåâÊó•ÊúüÊéíÂ∫èÔºàÈôçÂ∫èÔºâ
  result.sort((a, b) => {
    const dateA = a.updatedAt ? new Date(a.updatedAt).getTime() : 0
    const dateB = b.updatedAt ? new Date(b.updatedAt).getTime() : 0
    return dateA - dateB
  })
  
  return result.map(post => {
    // Ë∞ÉËØïÔºöÊâìÂç∞ÊØè‰∏™Â∏ñÂ≠êÁöÑÊó•Êúü
    console.log('ÂêéÁ´ØËøîÂõûÁöÑÂ∏ñÂ≠êÔºö', post);
    console.log('Â§ÑÁêÜÂ∏ñÂ≠êÊó•Êúü:', {
      id: post._id,
      createdAt: post.createdAt,
    })
    
    // ÊûÑÂª∫Ê†áÈ¢ò: Êó•ËÆ∞ÊòæÁ§∫Ê†áÈ¢òÔºåËØ¥ËØ¥ÊòæÁ§∫‰ΩçÁΩÆÊàñÂøÉÊÉÖ
    let title = post.title || ''
    if (!title) {
      if (post.location) {
        title = `üìç ${post.location}`
      } else if (post.mood) {
        const mood = moods.find(m => m.value === post.mood)
        title = mood ? `${mood.emoji} ${mood.text}` : ''
      } else {
        title = post.type === 'diary' ? 'Êó†Ê†áÈ¢òÊó•ËÆ∞' : ''
      }
    }
    
    // ÊûÑÂª∫ÊèèËø∞: ÂèØËÉΩÂåÖÂê´Â§©Ê∞îÁ≠â‰ø°ÊÅØ
    let description = post.content
    if (post.weather && post.type === 'diary') {
      const weatherEmoji = {
        'sunny': '‚òÄÔ∏è Êô¥Â§©',
        'rainy': 'üåßÔ∏è ‰∏ãÈõ®',
        'cloudy': '‚òÅÔ∏è Â§ö‰∫ë',
        'snowy': '‚ùÑÔ∏è ‰∏ãÈõ™'
      }
      description = `${weatherEmoji[post.weather] || ''}\n${description}`
    }
    
    return {
      time: post.updatedAt, // Áõ¥Êé•‰ΩøÁî®ÂêéÁ´ØËøîÂõûÁöÑÂàõÂª∫Êó∂Èó¥
      title: title,
      description: description,
      imageSrc: post.images && post.images.length > 0 ? post.images[0] : null,
      galleryImages: post.images || [], // Á°Æ‰øùÊòØÊï∞ÁªÑ
      id: post._id,
      postType: post.type,
      // Ê∑ªÂä†ÂéüÂßãÂ±ûÊÄß‰ª•‰æøÁºñËæëÊó∂Ê≠£Á°Æ‰øùÂ≠ò
      location: post.location,
      mood: post.mood,
      weather: post.weather
    }
  })
})

// ÂàùÂßãÂåñÊó∂Âä†ËΩΩÂ∏ñÂ≠ê
const loadPosts = async () => {
  await postStore.fetchPosts(activeFilter.value === 'all' ? '' : activeFilter.value)
}

// ÁõëÂê¨Á≠õÈÄâÂô®ÂèòÂåñÈáçÊñ∞Âä†ËΩΩÂ∏ñÂ≠ê
watch(activeFilter, async () => {
  await loadPosts()
})

// ÈáçÁΩÆË°®Âçï
const resetForm = () => {
  newPost.value = {
    title: '',
    content: '',
    images: [],
    uploadFiles: [], // Ê∏ÖÁ©∫‰∏ä‰º†Êñá‰ª∂ÂàóË°®
    location: '',
    mood: 'neutral',
    weather: 'sunny',
    type: postType.value
  }
  // Ê∏ÖÁ©∫Êñá‰ª∂ÂàóË°®
  fileList.value = []
  locationVisible.value = false
  weatherVisible.value = false
  showMoodSelector.value = false
  isEditing.value = false
  editingPostId.value = null
}

// Âà†Èô§Â∏ñÂ≠ê
const deletePost = async (id) => {
  const success = await postStore.removePost(id)
  if (success) {
    // ÈáçÊñ∞Âä†ËΩΩÂ∏ñÂ≠ê
    loadPosts()
  }
}

// ÂàùÂßãÂåñÊó∂Âä†ËΩΩÂ∏ñÂ≠ê
onMounted(() => {
  loadPosts()
})

// Â§ÑÁêÜÂõæÁâáÈ¢ÑËßà
const handlePictureCardPreview = (file) => {
  dialogImageUrl.value = file.url || URL.createObjectURL(file.raw)
  dialogVisible.value = true
}

// Â§ÑÁêÜÊñá‰ª∂ÈÄâÊã©
const handleFileChange = (file) => {
  // ‰øùÁïôÊñá‰ª∂ÂØπË±°Áî®‰∫é‰∏ä‰º†
  if (!newPost.value.uploadFiles) {
    newPost.value.uploadFiles = []
  }
  newPost.value.uploadFiles.push(file.raw)
  
  // ÂàõÂª∫‰∏¥Êó∂URLÁî®‰∫éÊòæÁ§∫
  const tempUrl = URL.createObjectURL(file.raw)
  file.url = tempUrl // Ê∑ªÂä†‰∏¥Êó∂URLÁî®‰∫éÈ¢ÑËßà
  
  // Âè™Âú®‰∏ä‰º†ÊàêÂäüÂêéÊâç‰ºöÊ∑ªÂä†Âà∞ÊúÄÁªàÁöÑimagesÊï∞ÁªÑ
}

// Â§ÑÁêÜÊñá‰ª∂ÁßªÈô§
const handleFileRemove = (file) => {
  // ÁßªÈô§‰∏ä‰º†Êñá‰ª∂ÂàóË°®‰∏≠ÁöÑÊñá‰ª∂
  if (newPost.value.uploadFiles) {
    const index = newPost.value.uploadFiles.findIndex(f => {
      return f === file.raw
    })
    if (index !== -1) {
      newPost.value.uploadFiles.splice(index, 1)
    }
  }
  
  // Â¶ÇÊûúÂ∑≤‰∏ä‰º†ÔºåÂàô‰πü‰ªéimages‰∏≠ÁßªÈô§
  if (file.url && file.url.startsWith('http')) {
    const imageIndex = newPost.value.images.indexOf(file.url)
    if (imageIndex !== -1) {
      newPost.value.images.splice(imageIndex, 1)
    }
  }
}

// ‰∏ä‰º†ÊâÄÊúâÈÄâ‰∏≠ÁöÑÂõæÁâáÂà∞OSS
const uploadImages = async () => {
  if (!newPost.value.uploadFiles || newPost.value.uploadFiles.length === 0) {
    return []
  }
  
  uploadLoading.value = true
  try {
    console.log('ÂáÜÂ§á‰∏ä‰º†ÂõæÁâáÔºåÊï∞Èáè:', newPost.value.uploadFiles.length);
    
    // Ë∞ÉÁî®OSSApi‰∏ä‰º†Â§ö‰∏™Êñá‰ª∂
    const imageUrls = await ossApi.uploadMultipleFiles(newPost.value.uploadFiles);
    
    console.log('ÂõæÁâá‰∏ä‰º†ÊàêÂäüÔºåURLÂàóË°®:', imageUrls);
    
    // Ê∏ÖÁ©∫‰∏ä‰º†Êñá‰ª∂ÂàóË°®
    newPost.value.uploadFiles = [];
    
    // ËøîÂõûÂõæÁâáURLÊï∞ÁªÑ
    return imageUrls || [];
  } catch (error) {
    console.error('‰∏ä‰º†ÂõæÁâáÂ§±Ë¥•:', error);
    ElMessage.error(`‰∏ä‰º†ÂõæÁâáÂ§±Ë¥•: ${error.message || 'Êú™Áü•ÈîôËØØ'}`);
    return [];
  } finally {
    uploadLoading.value = false;
  }
}

// ÊªöÂä®Âà∞‰∏ä‰º†Âå∫Âüü
const scrollToUpload = () => {
  document.getElementById('upload-section')?.scrollIntoView({ behavior: 'smooth' })
}

// ÊØèÊ¨°ÊòæÁ§∫ÂØπËØùÊ°ÜÊó∂ÔºåÊõ¥Êñ∞postTypeÂà∞newPost
watch(postType, (newVal) => {
  newPost.value.type = newVal
  console.log('ÂàáÊç¢Á±ªÂûã‰∏∫:', newVal)
})

// ÂàùÂßãÂåñÂØπËØùÊ°ÜÊó∂ËÆæÁΩÆÁ±ªÂûã
const openPostDialog = (type = 'thought') => {
  isEditing.value = false
  editingPostId.value = null
  postType.value = type
  newPost.value = {
    title: '',
    content: '',
    images: [],
    uploadFiles: [], // Á°Æ‰øù‰∏ä‰º†Êñá‰ª∂ÂàóË°®‰∏∫Á©∫
    location: '',
    mood: 'neutral',
    weather: 'sunny',
    type: type
  }
  // Ê∏ÖÁ©∫Êñá‰ª∂ÂàóË°®
  fileList.value = []
  showPostDialog.value = true
}

// ÁºñËæëÂ∏ñÂ≠ê
const editPost = (post) => {
  isEditing.value = true
  editingPostId.value = post.id
  postType.value = post.postType
  
  // Â°´ÂÖÖË°®ÂçïÊï∞ÊçÆ
  newPost.value = {
    title: post.title,
    content: post.description,
    images: post.galleryImages || [],
    uploadFiles: [], // Êñ∞‰∏ä‰º†ÁöÑÊñá‰ª∂ÂàùÂßã‰∏∫Á©∫
    location: post.location || '',
    mood: post.mood || '',
    weather: post.weather || '',
    type: post.postType,
    createdAt: post.time // ‰øùÁïôÂéüÂßãÂàõÂª∫Êó∂Èó¥
  }
  
  // ÂáÜÂ§áÊñá‰ª∂ÂàóË°®ÊòæÁ§∫Â∑≤ÊúâÂõæÁâá
  fileList.value = (post.galleryImages || []).map((url, index) => {
    return {
      name: `Â∑≤ÊúâÂõæÁâá${index + 1}`,
      url: url,
      status: 'success'
    }
  })
  
  // ÊòæÁ§∫Áõ∏ÂÖ≥ÈÄâÈ°π
  if (newPost.value.location) {
    locationVisible.value = true
  }
  if (newPost.value.weather) {
    weatherVisible.value = true
  }
  
  showPostDialog.value = true
}

// Â§ÑÁêÜÊèê‰∫§ÔºàÊñ∞Â¢ûÊàñÁºñËæëÔºâ
const handleSubmit = async () => {
  if (!isPostValid.value) return
  
  try {
    // ÊòæÁ§∫Âä†ËΩΩÊèêÁ§∫
    const loadingInstance = ElLoading.service({
      lock: true,
      text: 'Ê≠£Âú®Â§ÑÁêÜÂõæÁâáÂíåÊèê‰∫§ÂÜÖÂÆπÔºåËØ∑Á®çÂÄô...',
      background: 'rgba(0, 0, 0, 0.7)'
    })
    
    // ÂÖà‰∏ä‰º†ÊâÄÊúâÈÄâ‰∏≠ÁöÑÂõæÁâáÂà∞OSS
    let imageUrls = []
    if (newPost.value.uploadFiles && newPost.value.uploadFiles.length > 0) {
      imageUrls = await uploadImages()
    }
    
    // ÂáÜÂ§áÂ∏ñÂ≠êÊï∞ÊçÆÔºåÂåÖÂê´Â∑≤‰∏ä‰º†ÁöÑÂõæÁâáURLÂíåÂéüÊúâÂõæÁâáÔºàÂ¶ÇÊûúÊúâÔºâ
    let postImages = [...(newPost.value.images || [])]
    
    // Âè™Ê∑ªÂä†Êñ∞‰∏ä‰º†ÁöÑÂõæÁâáURL
    postImages = [...postImages, ...imageUrls]
    
    const postData = {
      ...newPost.value,
      images: postImages,
      // ËÆæÁΩÆÂàõÂª∫Êó∂Èó¥ÔºöÁºñËæëÊó∂‰øùÁïôÂéüÂßãÊó∂Èó¥ÔºåÊñ∞Âª∫Êó∂ËÆæÁΩÆÂΩìÂâçÊó∂Èó¥
      createdAt: isEditing.value ? newPost.value.createdAt : new Date().toISOString()
    }
    
    // Âà†Èô§‰∏ä‰º†Êñá‰ª∂ÂàóË°®ÔºåÈÅøÂÖçÂèëÈÄÅÂà∞ÂêéÁ´Ø
    delete postData.uploadFiles
    
    console.log('ÂáÜÂ§áÊèê‰∫§ÁöÑÂ∏ñÂ≠êÊï∞ÊçÆ:', postData);
    
    let success
    if (isEditing.value) {
      success = await postStore.updatePost(editingPostId.value, postData)
    } else {
      success = await postStore.addCustomPost(postData)
    }
    
    // ÂÖ≥Èó≠Âä†ËΩΩÊèêÁ§∫
    loadingInstance.close()
    
    if (success) {
      // Ê∏ÖÁ©∫Ë°®ÂçïÂπ∂ÂÖ≥Èó≠ÂØπËØùÊ°Ü
      resetForm()
      showPostDialog.value = false
      // ÈáçÊñ∞Âä†ËΩΩÂ∏ñÂ≠êÂàóË°®
      await loadPosts()
      
      ElMessage.success(isEditing.value ? 'Êõ¥Êñ∞ÊàêÂäü' : 'ÂèëÂ∏ÉÊàêÂäü')
    }
  } catch (error) {
    console.error('Êìç‰ΩúÂ§±Ë¥•:', error)
    ElMessage.error('Êìç‰ΩúÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÂÜçËØï')
  }
}
</script>

