import { defineStore } from 'pinia'
import { ref } from 'vue'
import { usePlantStore } from './plant'
import { postApi } from '../services/api'
import { ElMessage } from 'element-plus'

export const usePostStore = defineStore('post', () => {
  // ÊâÄÊúâÂ∏ñÂ≠êÊï∞ÊçÆ
  const posts = ref([])
  const loading = ref(false)

  // Ëé∑ÂèñÊâÄÊúâÂ∏ñÂ≠ê
  const fetchPosts = async (type = 'all') => {
    loading.value = true
    try {
      const response = await postApi.getPosts(type)
      posts.value = response.posts
      return response.posts
    } catch (error) {
      console.error('Ëé∑ÂèñÂ∏ñÂ≠êÂ§±Ë¥•:', error)
      ElMessage.error('Ëé∑ÂèñÂ∏ñÂ≠êÂàóË°®Â§±Ë¥•')
      return []
    } finally {
      loading.value = false
    }
  }

  // Ê∑ªÂä†Ëá™ÂÆö‰πâÂ∏ñÂ≠êÔºàÊîØÊåÅÊó•ËÆ∞ÂíåËØ¥ËØ¥Ôºâ
  const addCustomPost = async (postData) => {
    try {
      // Á°Æ‰øùÊúâÂàõÂª∫Êó∂Èó¥Â≠óÊÆµÔºåÂ¶ÇÊûúÂ∑≤ÁªèÊúâÂ∞±Áî®ÂéüÊù•ÁöÑÔºåÊ≤°ÊúâÂàôËÆæÁΩÆ‰∏∫ÂΩìÂâçÊó∂Èó¥
      const dataToSubmit = {
        ...postData,
        createdAt: postData.createdAt || new Date().toISOString()
      };
      console.log('ÂáÜÂ§áÊèê‰∫§Âà∞ÂêéÁ´ØÁöÑÂ∏ñÂ≠êÊï∞ÊçÆ:', dataToSubmit);
      
      const response = await postApi.createPost(dataToSubmit);
      await fetchPosts();
      return true;
    } catch (error) {
      console.error('ÂèëÂ∏ÉÂ§±Ë¥•:', error);
      ElMessage.error('ÂèëÂ∏ÉÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÂÜçËØï');
      return false;
    }
  }
  
  // ÂÖºÂÆπÊóßÁöÑÊ∑ªÂä†ËØ¥ËØ¥ÊñπÊ≥ï
  const addPost = async (content, images = [], location = '') => {
    return await addCustomPost({
      content,
      images,
      location,
      type: 'thought'
    })
  }
  
  // Âà†Èô§Â∏ñÂ≠ê
  const removePost = async (id) => {
    loading.value = true
    try {
      await postApi.deletePost(id)
      // Êõ¥Êñ∞Êú¨Âú∞Áä∂ÊÄÅ
      posts.value = posts.value.filter(post => post._id !== id)
      ElMessage.success('Âà†Èô§ÊàêÂäü')
      return true
    } catch (error) {
      console.error('Âà†Èô§Â∏ñÂ≠êÂ§±Ë¥•:', error)
      ElMessage.error('Âà†Èô§Â§±Ë¥•ÔºåËØ∑Á®çÂêéÂÜçËØï')
      return false
    } finally {
      loading.value = false
    }
  }
  
  // Êõ¥Êñ∞Â∏ñÂ≠ê
  const updatePost = async (id, postData) => {
    try {
      // Á°Æ‰øù‰øùÁïôÂéüÂßãÂàõÂª∫Êó∂Èó¥
      const dataToSubmit = {
        ...postData,
        // Â¶ÇÊûúÊèê‰æõ‰∫ÜÂàõÂª∫Êó∂Èó¥Â∞±‰ΩøÁî®ÂÆÉÔºåÂê¶Âàô‰ΩøÁî®ÂΩìÂâçÊó∂Èó¥
        createdAt: postData.createdAt || new Date().toISOString()
      };
      console.log('Êõ¥Êñ∞Â∏ñÂ≠êÊï∞ÊçÆ:', dataToSubmit);
      
      const response = await postApi.updatePost(id, dataToSubmit);
      await fetchPosts();
      ElMessage.success('Êõ¥Êñ∞ÊàêÂäü');
      return true;
    } catch (error) {
      console.error('Êõ¥Êñ∞Â§±Ë¥•:', error);
      ElMessage.error('Êõ¥Êñ∞Â§±Ë¥•ÔºåËØ∑Á®çÂêéÂÜçËØï');
      return false;
    }
  }
  
  // ÂàùÂßãÂåñÊó∂Âä†ËΩΩÂ∏ñÂ≠ê
  const init = () => {
    fetchPosts()
  }
  
  // Ëá™Âä®ÂàùÂßãÂåñ
  init()
  
  return {
    posts,
    loading,
    fetchPosts,
    addPost,
    addCustomPost,
    removePost,
    updatePost
  }
}) 